<template>
  <div id="hello">
    <h2 id="notice" :style="{ opacity: noticeOpacity }"></h2>
    <ul>
      <div class="title"><span>日常话术</span></div>
      <li class="box" v-for="(item, index) in items" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>软件使用话术</span></div>
      <li class="box" v-for="(item, index) in app" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>iOS & iPad话术</span></div>
      <li class="box" v-for="(item, index) in ios" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>Windows话术</span></div>
      <li class="box" v-for="(item, index) in windows" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>macOS话术</span></div>
      <li class="box" v-for="(item, index) in mac" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>Android话术</span></div>
      <li class="box" v-for="(item, index) in android" :key="index" @click="copyImageOrTextToClipboard(item)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <div class="preview" @click.stop="copyImageOrTextToClipboard(item)"></div>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>
    <div style="clear: both;"></div>
    <div style="height: 300px"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'HelloWorld',
  data() {
    return {
      noticeOpacity: 0,
    };
  },
  computed: {
    ...mapGetters(['items', 'app', 'ios', 'windows', 'mac', 'android'])
  },
  methods: {
    async copyToClipboard(data) {
      try {
        // 检查浏览器是否支持 Clipboard API
        if (!navigator.clipboard) {
          console.error('Clipboard API is not supported.');
          return;
        }

        // 将数据写入剪贴板
        await navigator.clipboard.writeText(data);

        console.log('内容已成功复制到剪贴板');
      } catch (err) {
        console.error('复制到剪贴板失败：', err);
      }
    },

    async copyImageOrTextToClipboard(item) {
      try {
        let data = '';

        if (item.content === '') {
          if (item.img === '') {
            console.error('无可复制内容');
            return;
          }

          const imgBlob = await fetch(item.img).then(response => response.blob());
          const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
          await navigator.clipboard.write([clipboardItemInput]);

          data = '图片';
        } else {
          await this.copyToClipboard(item.content);

          data = '文字';
        }

        this.noticeOpacity = 1;
        document.getElementById('notice').innerText = `${data}复制成功`;

        setTimeout(() => {
          this.noticeOpacity = 0;
          document.getElementById('notice').innerText = '';
        }, 1000);
      } catch (err) {
        console.error('复制到剪贴板失败：', err);
      }
    }
  }
}
</script>

